.navbar {
    grid-area: n;
    margin: 0 20px 20px 0;
    box-shadow: 1px 1px 3px #b9b9b9;
    background-color: #b9b9b9;
}

.navbar_burger {
    display: none;
    position: absolute;
    top: 1.5%;
    left: 13%;
    cursor: pointer;
    width: 50%;
    z-index: 2;
}

.navbar_burger i {
    color: #fff;
    width: 21px;
    font-size: 20px;
}

.burger_menu {
    background-color: #b9b9b9;
    border-bottom-left-radius: 22px;
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
}

.navbar_list_pages {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    gap: 20px;
    padding: 20% 0;
    text-align: center;
}

.navbar_list_pages a {
    text-decoration: none;
    font-size: 20px;
}

@media screen and (max-width: 1000px) {
    .navbar_list_pages a {
        font-size: 16px;
    }
}

@media screen and (max-width: 600px) {
    .navbar {
        display: none;
    }

    .navbar_burger {
        display: block;
    }
}