.login {
    width: 40%;
    margin: 0 auto;
    padding: 3%;
    box-shadow: 0px 0px 3px;
    grid-area: c;
}

.login_button{
    display: inline-block;
    margin-right: 5px;
    cursor: pointer;
}

.login_button a{
    color: #fff;
    text-decoration: none;
}

.dropdown_button{
    cursor: pointer;
}

.dropdown_menu{
    background-color: #e0e0e0;
    padding: 1%;
    text-align: center;
    position: absolute;
    margin-top: -10px;
}

.dropdown_menu a{
    height: 20px;
    color: #fff;
    text-decoration: none;
}

.dropdown_menu a:hover{
    text-decoration: underline;
}

.email {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 3%;
}

.email label {
    display: block;
    font-size: 1.2em;
    margin-bottom: 2%;
}

.email input {
    width: 80%;
    padding: 1%;
    font-size: 1.1em;
    border-radius: 10px;
}

.password {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 3%;
}

.password label {
    display: block;
    font-size: 1.2em;
}

.password input {
    width: 80%;
    padding: 1%;
    font-size: 1.1em;
    border-radius: 10px;
}

.remember {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 3%;
    display: grid;
    grid-template-columns: 1fr 10fr;
    grid-template-rows: 1fr;
}

.remember input {
    display: grid;
    place-content: center;
}

.remember input::before {
    content: "";
    width: 0.65em;
    height: 0.65em;
    transform: scale(0);
    transition: 120ms transform ease-in-out;
    box-shadow: inset 1em 1em var(--form-control-color);
}

.remember input:checked::before {
    transform: scale(1);
}

.remember label {
    font-size: 1.2em;
}

.captcha{
    width: 80%;
    margin: 0 auto;
    margin-bottom: 3%;
}

.captcha img{
    width: 60%;
    margin: 0 auto;
}

.captcha input{
    width: 80%;
    padding: 1%;
    font-size: 1.1em;
    border-radius: 10px;
}

.sendButton__block{
    width: 80%;
    margin: 0 auto;
}

.sendButton{
    border: 0;
    font-size: 1.1em;
    margin: 0 auto;
    width:100%;
    line-height: 50px;
}

.sendButton:hover{
    background-color: #818181;
}

.errorMessage{
    width: 80%;
    margin: 0 auto;
    background-color: #ff0000;
    padding: 5px;
    border-radius: 5px;
    margin-top: 10%;
    margin-bottom: 5%;
}

.errorMessage p {
    color: #fff;
    text-align: center;
}

@media screen and (max-width: 1000px){
    .login {
        width: 90%;
    }
}